.dropdown {
  position: relative;
  display: inline-block;
}

.dropbtn {
  background-color: rgba(25, 25, 25, 0.5);
  color: white;
  padding: 10px;
  font-size: 14px;
  border: none;
  border-radius: 5px 5px 0 0;
  cursor: pointer;
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dropbtn:hover {
  background-color: rgba(25, 25, 25, 0.7);
}

.dropdown-content {
  display: none;
  position: absolute;
  z-index: 1;
  display: flex;
  flex-direction: column;
  width: 150px;
  background-color: rgba(25, 25, 25, 1);
  border-radius: 0 0 5px 5px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  font-size: 14px;
}

.dropdown-item {
  padding: 6px 10px;
}

.dropdown-item:hover {
  background-color: black;
  cursor: pointer;
}

.dropdown-item input {
  margin-right: 6px;
}
